import { MessageBox,Toast,Indicator } from 'mint-ui'
import qs from 'qs'
export default {
  name: 'app',
  data () {
    return {
      declarantFaceImg:'',
      declarantRoomImgs:'',
      phone1:'',
      name:'',
      phone2:'',
      subFlag:false,
      popupVisible:false,
      pVisible:false,
      delFlag:false,
      values_index:0,
      values:{
        values0:"",
        values1:""
      },
      label:{
        label0:'请选择',
        label1:'请选择'
      },
      options: {
        tit:'',
        arr:[]
      },
      options0: {
        tit:'请选择居留类型  Residence type',
        arr:[
          {
            label: '工作类居留 Jobs',
            value: '1'
          },
          {
            label: '学习类居留 Learning',
            value: '2'
          },
          {
            label: '记者类居留 Reporter',
            value: '3'
          },
          {
            label: '团聚类居留 Reunion',
            value: '4'
          },
          {
            label: '私人事务类居留 Personal issue',
            value: '5'
          }
        ]
      },
      options1: {
        tit:'请选择关系  Ralationship',
        arr:[
          {
            label: '父母 Parents',
            value: '1'
          },
          {
            label: '夫妻 Couple',
            value: '2'
          },
          {
            label: '朋友 Friend',
            value: '3'
          },
          {
            label: '老师 Teacher',
            value: '4'
          },
          {
            label: '亲人 Relative',
            value: '5'
          },
          {
            label: '同事 Colleague',
            value: '6'
          },
          {
            label: '其他 Other',
            value: '7'
          }
        ]
      },
      photoTit:[
        ['人像信息','拍摄/上传申报人正面照'],
      ],
      picFunIndex:0,
      images:[],
      index:-1,
      lease:[],
      leaseSrc:[],
      imgSrc:{
        passport_img1:'',
        passport_img2:'',
        passport_img3:'',
        passport_img4:'',
      },
      uploadData:{
        images0:[],
        index0:-1,

        images1:[],
        index1:-1,

        images2:[],
        index2:-1,

        images3:[],
        index3:-1,

        images4:[],
        index4:-1,

        images5:[],
        index5:-1,

        images6:[],
        index6:-1,

        images7:[],
        index7:-1
      }
    }
  },
  computed:{
    // 预览图片路径
    preImages(){
      return this.uploadData['images'+this.picFunIndex].map(v=>{return v.src});
    },
  },
  methods:{
    htmlScroll(){
      this.timer = setTimeout(() => {
        window.scrollTo(0,0)
        // 间隔设为10，减少页面失去焦点定时器的突兀感，
      },10)
    },
    handleVisibleChange (isVisible) {
        this.pVisible = isVisible;
    },
    getList(e){
      e==0?this.options = this.options0:this.options = this.options1;
       this.values_index=e;
       this.popupVisible=true;
    },
    radioFun(){
      for(var i=0;i<this.options.arr.length;i++){
        if(this.values['values'+this.values_index]==this.options.arr[i].value){
          this.label['label'+this.values_index]=this.options.arr[i].label
        }
      }
      this.popupVisible=false;
      this.checkForm();
    },
    checkForm(){
      if(this.lease.length<3||this.declarantFaceImg.length==0){
        this.subFlag = false;
      }else{
        this.subFlag = true;
      }
    },
    formFun(){
      let that = this;
      MessageBox({
       title: '温馨提示',
       message: '确认提交信息？',
       showCancelButton: true,
       cancelButtonText:'取消',
       confirmButtonText:'确定'
     }).then(action => { 
       if (action == 'confirm') {     //确认的回调
        that.commitResult();
       }
     });
    },
    commitResult(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'post',
         url: '/foreignersDwell/checkDeclare/h5/commitResult',
         data: {
           declareId:sessionStorage.declareId,
           lgyUserName:sessionStorage.userName,
           checkStatus:1,
           declarantFaceImg:that.declarantFaceImg,
           declarantRoomImgs:that.lease.join(',')
         }
       }).then(function (res) {
         Indicator.close();
         if(res.data.error==0){
           sessionStorage.result=1
         }else {
           sessionStorage.result=2
         }
         that.$router.push({
           name:'result'
         })
       })
    },
    picFun(e){
      this.picFunIndex=e;
      this.images=this.uploadData['images'+this.picFunIndex];
    },
    popFun(){
      this.popupVisible=false;
    },
    /**
     *  绑定函数 -- 选择图片
     */
    bindtap_chooseImages(e){
      let that = this;
      Indicator.open('加载中');
      that.checkForm();
      if (Array.isArray(e)) {
        //this.images = this.images.concat(res);
        that.axios.post('/intranet_uploadBase64',{
          fileBase64:e[0].src,
           isInternal:true //true 内网  false cos
        } )
         .then(function (res) {
           Indicator.close();
           if(res.data.error==0){
             if(that.picFunIndex<4){
               that.declarantFaceImg=res.data.data;
             }else{
               that.lease.push(res.data.data);
               console.log(that.lease);
             }
             that.checkForm();
             that.uploadData['images'+that.picFunIndex] = that.uploadData['images'+that.picFunIndex].concat(e);
           }else {
             Toast({
               message: res.data.message,
               duration: 1500
             });
           }


         })
         .catch(function (error) {
           oast({
             message: error.message,
             duration: 1500
           });
         });
        //this.bindtap_upload();
      }else {
        console.log(res);
      }
    },
    /**
     *  绑定函数 -- 删除图片
     */
    bindtap_delete(){
      this.images = this.images.filter((v,i) => {
        return this.index!=i;
      })
    },
    /**
     *  绑定函数 -- 预览图片
     */
    bingtap_preview(i,j){
      this.delFlag = true;
      this.picFunIndex=i;
       this.uploadData['index'+i] = j;
    },
    /**
     *  绑定函数 -- 取消预览
     */
    bingtap_hiddenImg() {
      this.delFlag = false;
      this.uploadData['index'+this.picFunIndex] = -1;
    },
    /**
     *  绑定函数 -- 上传图片
     */
     delFun(){
       let that = this;
       MessageBox({
        title: '确认删除图片吗？',
        message: 'Confirm delete image?',
        showCancelButton: true,
        cancelButtonText:'取消 Cancel',
        confirmButtonText:'确定 Confirm'
      }).then(action => { 
        if (action == 'confirm') {     //确认的回调
           if(this.picFunIndex<4){
             this.declarantFaceImg='';
           }else{
            this.lease = this.lease.slice(0,-1);
           }
           this.delFlag = false;
           this.uploadData['index'+this.picFunIndex] = -1;
           this.uploadData['images'+this.picFunIndex]=[];
           this.checkForm();
        }
      });
     }
  },

}



