import { MessageBox,Toast } from 'mint-ui';
export default {
  name: 'app',
  data () {
    let that = this;
    console.log(that)
    return {
      res:{
        suc:{
          img:'suc',
          txt:'提交成功，请等待审核'
        },
        err:{
          img:'err',
          txt:'提交失败，此审核已被核查'
        }
      },
      resultIndex :'',
      result:''

    }
  },
  methods:{
    toIndex:function(){
      location.replace('#/home');
    }
  },
  created:function (){
    this.resultIndex=sessionStorage.result;
    sessionStorage.result==1?this.result = this.res.suc: this.result = this.res.err;
  }

}



