          //    //    name:'login'
          //    //  })
          //    // }else {
          //    //   Toast({
          //    //     message: res.data.message,
          //    //     position: 'bottom',
          //    //     duration: 1500
          //    //   });
          //    // }
          //  })
       }
     });;
   },

   listFun:function(){
     let that =this;
     Indicator.open('加载中');
     this.axios({
        method: 'get',
        url: '/foreignersDwell/checkDeclare/h5/getRecheckList?lgyUserName='+sessionStorage.userName,
      }).then(function (res) {
        Indicator.close();
        if(res.data.error==0){
          store.commit('listFun',res.data.data.checkList);
          that.$router.push({
            name:"list"
            // path:'/home'
          });
        }else {
          Toast({
            message: res.data.message,
            position: 'bottom',
            duration: 1500
          });
        }
      })

   },
 }

}



