import { MessageBox,Toast,Indicator } from 'mint-ui'
import qs from 'qs'
import store from '../store.js'

export default {
  name: 'app',
  data () {
    return {
      notVerifyList:[],
      blank:false
    }
  },
  created:function(){
    let that =this;
    Indicator.open('加载中');
    this.axios({
       method: 'get',
       url: '/foreignersDwell/checkDeclare/h5/getNotVerifyList?lgyUserName='+sessionStorage.userName,
     }).then(function (res) {
       Indicator.close();
       if(res.data.error==0){
         that.notVerifyList=res.data.data.notVerifyList;
       }else {
         Toast({
           message: res.data.message,
           position: 'bottom',
           duration: 1500
         });
       }
     })
  },
  methods:{
    outFun:function(){
      this.$router.push({
        name:"main"
        // path:'/home'
      })
    },
    listFun:function(){
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/foreignersDwell/checkDeclare/h5/getRecheckList?lgyUserName='+sessionStorage.userName,
       }).then(function (res) {
         Indicator.close();
         if(res.data.error==0){
           store.commit('listFun',res.data.data.checkList);
           that.$router.push({
             name:"list"
             // path:'/home'
           });
         }else {
           Toast({
             message: res.data.message,
             position: 'bottom',
             duration: 1500
           });
         }
       })

    },
    infoFun(e){
      sessionStorage.declareId=e;
      let that =this;
      Indicator.open('加载中');
      this.axios({
         method: 'get',
         url: '/foreignersDwell/checkDeclare/h5/getDeclareDetail?declareId='+e
       }).then(function (res) {
         Indicator.close();
         if(res.data.error==0){
           store.commit('infoFun',res.data.data.declareDetail);
           that.$router.push({
             name:'info'
           })
         }else {
           Toast({
             message: res.data.message,
             position: 'bottom',
             duration: 1500
           });
         }
       })
    },
  }
}



